import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController, AlertController, Loading, LoadingController } from 'ionic-angular';
import { SessionProvider } from '../../providers/session/session';
import { AuthService } from '../../providers/auth-service/auth-service';
import { FileUploader, FileUploaderOptions } from 'ng2-file-upload';

export interface Session {
  name: string,
  desc: string,
  files: Array<any>
}

@IonicPage({
  name: 'create-page'
})
@Component({
  selector: 'page-create',
  templateUrl: 'create.html',
})
export class CreatePage {
  uploadFiles = [];
  public newSession: Session;

  public url:string = 'session/create';
  public queueLimit:number;
  public maxFileSize:number;
  public autoUpload:boolean = false;
  public allowedMimeType:Array<string> = ['image/*', 'audio/*', 'video/*'];
  public allowedFileType:Array<string>;
  public headers:Array<any> = [{Authorization: localStorage.getItem('id_token')}];

  private inputs:string[] = ['allowedMimeType',
      'allowedFileType',
      'autoUpload',
      'isHTML5',
      'headers',
      'maxFileSize',
      'queueLimit',
      'removeAfterUpload',
      'url'
  ];

  public loading: Loading;

  private uploaderOptions:FileUploaderOptions = {};
  private multiple:boolean = true;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private session: SessionProvider,
    private actionSheetCtrl: ActionSheetController,
    private auth: AuthService,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private fileUploadService: FileUploader) {
  }

  public ngOnInit():any {
    for (let input of this.inputs) {
      if (this[input]) {
        this.uploaderOptions[input] = this[input];
      }
    }
    this.multiple = (!this.queueLimit || this.queueLimit > 1);
    this.fileUploadService.setOptions(this.uploaderOptions);

    this.fileUploadService.onBeforeUploadItem = (fileItem: any) => {
      fileItem.formData.push( { name: this.newSession.name } );
      fileItem.formData.push( { desc: this.newSession.desc } );
    };

    this.fileUploadService.onCompleteAll = () => {
      this.loading.dismiss();
    }

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreatePage');
  }

  onFileChoose(e) {
    let files = e.srcElement.files;
    // let fr: FileReader = new FileReader();
    // for(let file of files) {
    //   fr.readAsDataURL(file);
    //   fr.onload = (event) => {
    //     file['dataUrl'] = fr.result;
    //     this.uploadFiles.push(file);
    //   }
    //   fr.onerror = (event) => {
    //     this.showError('Error in reading the file!');
    //   }
    // }
    console.log(files);
  }

  mediaCapture() {

  }

  // createSession(form) {
  //   console.log(form.value);
  //   console.log(this.uploadFiles);
  //   if(form.valid){
  //     let newSession: Session = {
  //       name: form.value.name,
  //       desc: form.value.desc,
  //       files: this.uploadFiles
  //     }
  //     this.session.createSession(newSession).subscribe( data => {
  //       console.log(data);
  //     });
  //   }
  // }

  createSession(form) {
    this.showLoading();
    console.log(form);
    this.newSession.name = form.value.name;
    this.newSession.desc = form.value.desc;
    this.fileUploadService.authTokenHeader = localStorage.getItem('id_token');
    this.fileUploadService.uploadAll();
  }

  showLoading() {
    this.loading = this.loadingCtrl.create({
      content: 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }

  uploadSheet() {
     let actionSheet = this.actionSheetCtrl.create({
       title: 'Select Image Source',
       buttons: [
         {
           text: 'Upload from Gallery',
           handler: () => {
            //  this.takePicture(this.camera.PictureSourceType.PHOTOLIBRARY);
           }
         },
         {
           text: 'Use Camera',
           handler: () => {
            //  this.takePicture(this.camera.PictureSourceType.PHOTOLIBRARY);
           }
         },
         {
           text: 'Upload Audio',
           handler: () => {
            //  this.takePicture(this.camera.PictureSourceType.CAMERA);
           }
         },
         {
           text: 'Cancel',
           role: 'cancel'
         }
       ]
     });
     actionSheet.present();
   }

   showError(text) {
     let alert = this.alertCtrl.create({
       title: 'Fail',
       subTitle: text,
       buttons: ['OK']
     });
     alert.present(prompt);
   }


   logout() {
     this.navCtrl.setRoot('login-page');
     this.auth.logout();
   }


}



